import axios from 'axios';
import { ServerConfig } from './serverConfig';

export async function getProjects() {

    try {


        // Requte Axios pour remplacer le deuxime appel AJAX
        const res = await axios.get(ServerConfig.serverUrl + 'projects');
        const data = res.data;


        return (data)
    } catch (error) {
        console.error("Erreur lors de la rcupration des donnes :", error);
    }
}
