import axios from 'axios';
import { ServerConfig } from './serverConfig';


export async function getUsers() {

    try {
        // Requte Axios pour remplacer le premier appel AJAX
        const res = await axios.get(ServerConfig.serverUrl + "users");
        const data = res.data;

        return data
    } catch (error) {
        console.error("Erreur lors de la rcupration des donnes user :", error);
    }
}
